@import url(https://juancamejoalarcon.s3.eu-west-3.amazonaws.com/juancamejoalarcon.css);
:root {
    --primary-font: 'Roboto', sans-serif;
    --secondary-font: 'Cabin', sans-serif;
    --jca-color-1: '#6e41ff';
    --jca-color-2: '#f5f9fe';
    --jca-color-3: '#d0d0d0';
    --jca-color-4: '#333333';
    --jca-color-5: '#666666';
    --font-regular: 300;
    --font-bold: 400;
    --font-extrabold: 700;
}

ul,
ol {
    list-style: none;
    padding: 0;
    margin: 0;
}

body {
    background-color: blue;
}
.header{background-color:transparent;position:fixed;top:0;left:0;width:100%;z-index:999}.header__container{max-width:1140px;width:100%;padding-right:15px;padding-left:15px;margin:auto}.header__navbar{background-color:transparent;padding:0 0;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}.header__navbar__logo{padding:0px 10px;max-width:150px}.header__navbar__logo img{width:100%}.header__navbar__nav{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;width:100%;max-width:700px}.header__navbar__nav__menu li{float:left;position:relative}.header__navbar__nav__menu li a{padding:35px 16px;display:inline-block;text-transform:capitalize;transition:all 0.3s;font-family:var(--primary-font);font-weight:var(--font-extrabold);font-size:0.85rem;color:var(--jca-color-4)}.header__navbar__nav__menu li a:hover{color:#b4b1bb}.header__navbar__nav__menu li:hover .sub-menu{top:100%;opacity:1;visibility:visible}.header__navbar__nav__menu .sub-menu{position:absolute;top:110%;min-width:220px;background-color:#ffffff;box-shadow:0px 18px 54px -8px rgba(0,0,0,0.15);padding:15px 0;z-index:999;opacity:0;visibility:hidden;transition:all 0.3s}.header__navbar__nav__menu .sub-menu li{float:none}.header__navbar__nav__menu .sub-menu li a{padding:8px 25px;transition:all 0.3s}.header__navbar__nav__menu .sub-menu li a:hover{padding-left:35px;color:var(--jca-color-4)}.header__navbar__nav__menu .menu_has_children::after{position:absolute;content:"ˆ";top:36px;right:0px;color:#000;z-index:-10;transition:all 0.3s;z-index:2}.header__navbar__nav__menu .menu_has_children:hover::after{content:"ˇ"}.header__navbar__nav__social{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.header__navbar__nav__social .jca-social-icons{margin-top:-20px}

main{margin-top:120px}

